/* css page : createEmployee */

input,
select {
    border-radius: 12px;
    height: 30px;
    font-size: 18px;
    border-width: 2px;
    border-style: outset;
    border-color: gray;
    padding: 0px 0px 0px 10px;
}

h2 {
    margin: 0;
}

a {
    text-decoration: none;
}

a:hover {
    text-decoration: underline;
}


/*style header*/

.header {
    display: flex;
    justify-content: space-between;
    align-items: baseline;
    width: 90%;
    margin: auto;
    margin-top: 20px;
}

.containerTitle {
    width: 115px;
    height: 110px;
    background-color: #035728;
    border-radius: 50%;
}

.title {
    width: 115px;
    height: 110px;
    color: #fffefe;
    display: flex;
    margin: 0px;
    justify-content: center;
    align-items: center;
}

.nav {
    font-size: 20px;
    color: black;
    font-weight: bold;
}

.elementNav {
    display: flex;
    flex-direction: row;
    align-items: center;
    width: 430px;
    justify-content: space-between;
}


/*style formulaire*/

#createEmployee {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 35px;
}

label {
    font-size: 18px;
    font-weight: bold;
    display: flex;
    flex-direction: column;
}

fieldset {
    width: 730px;
    margin: 13px 15px;
    border: 3px solid #035728;
    border-radius: 5px;
    padding: 20px 40px;
    grid-gap: 11px 100px;
    display: grid;
    grid-template-columns: 1fr 1fr;
}

.street {
    grid-area: street;
}

.city {
    grid-area: city;
}

.state {
    grid-area: state;
}

.zipCode {
    grid-area: zipCode;
}

.fieldsetAdress {
    grid-template-areas: "street street" "city state" "zipCode . ";
}

legend {
    width: 100px;
    padding: 7px 35px 7px 23px;
    background: #035728;
    border-radius: 5px;
    font-size: 18px;
    color: #fffefe;
}

.btnSave {
    width: 110px;
    height: 40px;
    border: none;
    background-color: #035728;
    color: #fffefe;
    font-size: 16px;
    border-radius: 5px;
    margin-top: 8px;
}

.btnSave:hover {
    animation: pulse 1s;
    box-shadow: 0 0 0 0.8em transparent;
}

@keyframes pulse {
    0% {
        box-shadow: 0 0 0 0 #0357278e;
    }
}

.errorMessageInput,
.errorMessageSelect,
.errorMessageDate {
    color: rgb(255 1 1 / 71%);
    font-size: 16px;
    font-style: italic;
    width: max-content;
}

.errorMessageInput {
    margin: -29px 0px 0px 8px;
}

.errorMessageSelect {
    margin: -27px 0px 0px 8px;
}

.errorMessageDate {
    margin: 0;
}


/* style Modal */

.modal {
    display: none;
    position: fixed;
    z-index: 2;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    overflow: auto;
    background-color: rgba(0, 0, 0, 0.77);
}

.textModal {
    font-weight: bold;
    font-size: 18px;
    line-height: 27px;
}

#confirmation {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    width: 430px;
    border-radius: 10px;
    padding: 0px 0px 0px 25px;
    height: 65px;
    background-color: white;
    position: fixed;
    top: 29%;
    left: 33%;
    font-weight: bold;
    font-size: 18px;
    line-height: 60px;
    box-shadow: 0px 2px 15px 6px #035728;
}

.btnClose {
    color: white;
    background-color: black;
    border-radius: 25px;
    width: 25px;
    height: 25px;
    margin-bottom: 57px;
    margin-right: -9px;
}


/* css page : EmployeeList */

.currentEmployee {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 40px;
}

.arrow {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.containerTable {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 0px auto;
    width: 90%;
}

.searchForm {
    height: 50px;
    width: 97%;
    justify-content: center;
}

.search {
    width: 100%;
    display: flex;
    flex-direction: row;
    font-size: 15px;
    justify-content: flex-end;
    align-items: center;
}

#search {
    width: 120px;
    margin-left: 12px;
    border-radius: 5px;
    border-width: 1px;
    padding: 0px 0px 0px 5px;
    font-size: 14px;
    height: 22px;
}

.dataTable {
    width: 90%;
    margin: 40px auto;
}

.tableCell {
    width: 126px;
}

.notFound {
    text-align: center;
    margin: 0;
}